import styled from "styled-components";
import Link from "next/link";

const NavButtons = ({
  labels,
  activeIndex
}: {
  labels: string[];
  activeIndex: number;
}) => (
  <NavButtonsArea>
    {labels.map((label, index) => (
      <Link key={label} href={`/${label.toLowerCase()}`}>
        {index === activeIndex ? (
          <ActiveNavButton>{label}</ActiveNavButton>
        ) : (
          <NavButton onClick={() => console.log("click")}>{label}</NavButton>
        )}
      </Link>
    ))}
  </NavButtonsArea>
);

export default NavButtons;

const NavButtonsArea = styled.div`
  display: flex;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.6);
  flex-direction: row;
  align-items: baseline;
  width: 100%;
  overflow-x: scroll;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;

  white-space: nowrap;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
`;

const ActiveNavButton = styled.a`
  display: flex;
  justify-content: center;
  margin-right: 1.5vw;
  font-size: 15px;
  letter-spacing: 3px;
  text-transform: uppercase;
  padding: 23px 15px;
  color: rgba(255, 255, 255, 1);
  border-bottom: 2px solid rgba(255, 255, 255, 1);
`;

const NavButton = styled.a`
  margin-right: 1.5vw;
  padding: 23px 15px;
  letter-spacing: 3px;
  text-transform: uppercase;
  border-bottom: 2px solid rgba(255, 255, 255, 0);
  &:hover {
    color: rgba(255, 255, 255, 1);
    border-bottom: 2px solid rgba(255, 255, 255, 1);
  }
`;

